<template>
    <uc class="m-dashboard-pwd">
        <div class="m-profile-pwd">
            <form v-if="status" class="m-dashboard-pwd-doing">
                <svg height="512" viewBox="0 0 512 512" width="512" xmlns="http://www.w3.org/2000/svg" v-bind:class="'u-pic'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M509.984 334.503a14.94 14.94 0 01-6.338 5.938l-32.32 15.981a15 15 0 01-14.149-.456L271.722 248.893c-16.227 12.244-35.316 19.908-55.809 22.322-24.735 2.915-49.24-2.143-70.864-14.628-54.916-31.706-73.798-102.177-42.093-157.093s102.177-73.798 157.093-42.093c21.624 12.485 38.257 31.177 48.1 54.056 8.156 18.955 11.063 39.318 8.573 59.493l68.939 39.802 31.449-2.028a14.998 14.998 0 0114.411 8.32l11.549 23.357 26.002-1.677a15 15 0 0115.043 9.791l26.962 73.301a14.994 14.994 0 01-1.093 12.687z" fill="#fa9100"/><path d="M343.493 464.426a14.942 14.942 0 01-8.458 1.974l-35.981-2.32a15 15 0 01-12.025-7.469L179.956 271.155c-20.175 2.49-40.538-.417-59.493-8.573-22.879-9.843-41.571-26.476-54.056-48.1C34.702 159.566 53.584 89.095 108.5 57.389s125.387-12.823 157.093 42.093c12.485 21.624 17.543 46.128 14.628 70.864-2.415 20.493-10.078 39.582-22.322 55.809l39.802 68.939 28.25 13.968a15 15 0 018.32 14.411l-1.677 26.002 23.357 11.549a15 15 0 018.132 16l-13.301 76.962a14.992 14.992 0 01-7.289 10.44z" fill="#a76100"/><path d="M114.991 512c-2.871 0-5.765-.822-8.312-2.52l-30-20A15 15 0 0170 477V262.854c-18.717-7.931-34.898-20.63-47.236-37.171C7.872 205.72 0 181.969 0 157 0 93.589 51.589 42 115 42s115 51.589 115 115c0 24.969-7.872 48.72-22.764 68.684-12.338 16.541-28.519 29.24-47.236 37.171v79.604l17.481 26.221a15 15 0 010 16.641L163.028 407l14.453 21.68a15 15 0 01-.958 17.923l-50 60A14.983 14.983 0 01114.991 512z" fill="#ffe16e"/><path d="M126.524 506.603l50-60a15.002 15.002 0 00.958-17.923L163.028 407l14.453-21.68a15 15 0 000-16.641L160 342.458v-79.604c18.717-7.931 34.898-20.63 47.236-37.171C222.129 205.72 230 181.969 230 157c0-63.411-51.589-115-115-115v470a14.987 14.987 0 0011.524-5.397z" fill="#ff8e00"/><path d="M100 117V15c0-8.284 6.716-15 15-15s15 6.716 1