                <el-alert
                    class="u-ac"
                    title="请妥善保管"
                    type="warning"
                    description="未绑定邮箱用户将无法使用找回密码功能"
                    show-icon
                    :closable="false"
                >
                </el-alert>

                <!-- 新密码 -->
                <div class="u-pass">
                    <el-input
                        class="u-text"
                        placeholder="新密码"
                        v-model="pwd1"
                        show-password
                        @change="checkPass"
                    >
                        <template slot="prepend">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'i-pass'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M437.333 192h-32v-42.667C405.333 66.99 338.344 0 256 0S106.667 66.99 106.667 149.333V192h-32A10.66 10.66 0 0064 202.667v266.667C64 492.865 83.135 512 106.667 512h298.667C428.865 512 448 492.865 448 469.333V202.667A10.66 10.66 0 00437.333 192zM287.938 414.823a10.67 10.67 0 01-10.604 11.844h-42.667a10.67 10.67 0 01-10.604-11.844l6.729-60.51c-10.927-7.948-17.458-20.521-17.458-34.313 0-23.531 19.135-42.667 42.667-42.667s42.667 19.135 42.667 42.667c0 13.792-6.531 26.365-17.458 34.313l6.728 60.51zM341.333 192H170.667v-42.667C170.667 102.281 208.948 64 256 64s85.333 38.281 85.333 85.333V192z"/></svg>
                        </template>
                    </el-input>
                    <div class="u-tip">
                        <el-alert
                            v-show="pass_validate == false"
                            :title="pass_validate_tip"
                            type="error"
                            show-icon
                            :closable="false"
                        ></el-alert>
                    </div>
                </div>

                <!-- 重复密码 -->
                <div class="u-pass">
                    <el-input
                        class="u-text"
                        placeholder="重复密码"
                        v-model="pwd2"
                        show-password
                        @input="checkPass"
                    >
                        <template slot="prepend">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'i-pass'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M437.333 192h-32v-42.667C405.333 66.99 338.344 0 256 0S106.667 66.99 106.667 149.333V192h-32A10.66 10.66 0 0064 202.667v266.667C64 492.865 83.135 512 106.667 512h298.667C428.865 512 448 492.865 448 469.333V202.667A10.66 10.66 0 00437.333 192zM287.938 414.823a10.67 10.67 0 01-10.604 11.844h-42.667a10.67 10.67 0 01-10.604-11.844l6.729-60.51c-10.927-7.948-17.458-20.521-17.458-34.313 0-23.531 19.135-42.667 42.667-42.667s42.667 19.135 42.667 42.667c0 13.792-6.531 26.365-17.458 34.313l6.728 60.51zM341.333 192H170.667v-42.667C170.667 102.281 208.948 64 256 64s85.333 38.281 85.333 85.333V192z"/></svg>
                        </template>
                    </el-input>
                    <div class="u-tip">
                        <el-alert
                            v-show="pwd2 && accordance == false"
                            :title="pass_accordance_tip"
                            type="error"
                            show-icon
                            :closable="false"
                        ></el-alert>
                    </div>
                </div>

                <!-- 提交 -->
                <el-button
                    class="u-submit u-button"
                    type="primary"
                    @click="done"
                    :disabled="!ready"
                    >提交</el-button
                >
            </form>
            <div v-else class="m-dashboard-pwd-done">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M256 0C115.3 0 0 115.3 0 256s115.3 256 256 256 256-115.3 256-256S396.7 0 256 0z" fill="#7d0"/><path d="M512 256c0 140.7-115.3 256-256 256V0c140.7 0 256 115.3 256 256z" fill="#6b0"/><path fill="#e7e7e7" d="M401.8 212.5L226 388.299l-126.301-126 42.602-42.6 83.699 84 30-30L359.5 170.2z"/><path fill="#d3d3d8" d="M401.8 212.5L256 358.299v-84.6L359.5 170.2z"/></svg>
                <p>修改成功</p>
                <el-button
                    class="u-submit u-button"
                    type="primary"
                    @click="reset"
                    >返回</el-button
                >
            </div>
        </div>
    </uc>
</template>

<script>
import uc from "@/components/uc.vue";
import { validator } from "sterilizer";
import User from "@jx3box/jx3box-common/js/user";
import { updatePassword } from "../service/profile";
export default {
    name: "pwd",
    props: [],
    data: function() {
        return {
            pwd1: "",
            pwd2: "",
            pass_validate: null,
            pass_validate_tip: "密码有效长度为6-50个字符",
            pass_accordance_tip: "两次密码不一致",
            status: true,
        };
    },
    computed: {
        accordance: function() {
            return this.pwd1 === this.pwd2;
        },
        ready: function() {
            return this.pass_validate && this.accordance;
        },
    },
    methods: {
        checkPass: function() {
            // 如果为空
            if (this.pwd1 == "") {
                this.pass_validate = null;
            }

            // 校验第1个值
            this.pass_validate = validator(this.pwd1, {
                len: [6, 50],
            });
        },
        done: function() {
            updatePassword({
                uid: User.getInfo().uid,
                pwd1: this.pwd1,
                pwd2: this.pwd2,
            }).then((res) => {
                this.$message({
                    message: "密码修改成功",
                    type: "success",
                });
                this.status = false;
            });
        },
        reset: function() {
            this.status = true;
        },
    },
    mounted: function() {},
    components: {
        uc,
    },
};
</script>

<style lang="less">
@import "../assets/css/profile/pwd.less";
</style>
